<template>
    <div class="app-catalog-page container">
        <!-- <AppSearchComponents>
            
        </AppSearchComponents> -->
        <div class="app-catalog-page__block-content">
            <div class="app-card-page__block">
                <AppButton width="160px" :hasBlack="true" :hasAround="true">
                    <router-link to="/AppCatalogPage" class="link">Купить</router-link>
                </AppButton>
            </div>
            <AppFilter>
                
            </AppFilter>
        </div>
        <!-- <label><input class="app-input" v-model="filter" type="radio" value="all"> All</label>
        <label><input class="app-input" v-model="filter" type="radio" value="finished"> Finished</label>
        <label><input class="app-input" v-model="filter" type="radio" value="unfinished"> Unfinished</label> -->
        <!-- <ul>
            <li v-for="todo in filteredTodos" :key="todo.id">
            <input class="app-input" v-model="todo.isFinished" type="checkbox">
            {{ todo }}
            {{ todo.minPrice }}
            </li>
        </ul> -->
        <!-- <button :disabled="!newTodoText" @click="addTodo">Add</button> -->
        <div class="app-catalog-page__card-content">
            <AppCardItem
                v-for="(array, index) in filteredTodos"
                :key="index"
                :card="array"
            >
            
            </AppCardItem>
        </div>
        <div class="app-catalog-page__block-button">
            <AppButton width="100%">
                Показать ещё
            </AppButton>
            <AppButton width="100%" :hasOutline="true" :hasWhite="true">
                Нужна помощь специалиста?
            </AppButton>
        </div>
    </div>
</template>

<script>
import AppCardItem from '../components/AppCardItem.vue';

// import AppTestInputComponent from './AppTestInputConpoment.vue';
import AppFilter from '../components/AppFilter.vue';

import { ref } from 'vue'
import { storeToRefs } from 'pinia'
import { useTodos } from '@/assets/api/apiFooter';

import AppButton from '../components/AppButton.vue';
// import AppSearchComponents from './AppSearchComponents.vue';

export default {
    name: "PageCatalog",
    components: {
        AppCardItem,
        AppFilter,
        AppButton
    },
    setup() {
        let cardObject = useTodos();
        const todosStore = useTodos()
        const { filter, filteredTodos } = storeToRefs(todosStore)

        const newTodoText = ref('')
        const newTodoText2 = ref('')
        const newTodoText3 = ref('')
        const newTodoText4 = ref('')
        const newTodoText5 = ref('')
        const newTodoText6 = ref('')
        const newTodoText7 = ref('')
        const newTodoText8 = ref('')
        const newTodoText9 = ref('')
        const newTodoText10 = ref('')
        const newTodoText11 = ref('')

        function addTodo() {
        if (!newTodoText.value) {
            return
        }

        todosStore.addTodo(
            newTodoText.value, 
            newTodoText2.value, 
            newTodoText3.value, 
            newTodoText4.value, 
            newTodoText5.value, 
            newTodoText6.value, 
            newTodoText7.value, 
            newTodoText8.value, 
            newTodoText9.value, 
            newTodoText10.value, 
            newTodoText11.value, 
            )

        newTodoText.value = ''
        newTodoText2.value = ''
        newTodoText3.value = ''
        newTodoText4.value = ''
        newTodoText5.value = ''
        newTodoText6.value = ''
        newTodoText7.value = ''
        newTodoText8.value = ''
        newTodoText9.value = ''
        newTodoText10.value = ''
        newTodoText11.value = ''

        }
        return {
            cardObject,
            filter,
            filteredTodos,
            addTodo
        };
    },
}

</script>

<style lang="scss" scoped>

.app-catalog-page {
    margin-bottom: 100px;

    &__block-content {
        margin: 40px auto;
    }

    &__card-content {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px 30px;
        width: 100%;
        max-width: 500px;
        height: 100%;
    }

    &__block-button {
        margin-top: 40px;
        display: grid;
        gap: 10px;
    }

    &__block {
        display: flex;
        gap: 10px;
    }
    
}

.link {
    text-decoration: none;
    color: #ffffff;
}

</style>